.teachingContainer{
    padding: 2vw;
    text-align: center;
    width: 100%;
}

blockquote p {
    font-size: 50px;
}

Footer p{
    font-size: 40px;
}

.teachingContainer h3{
    font-size: 40px;
    margin-top:50px;
}

blockquote p:before{
    content: open-quote;
    font-weight: bold;
    font-size: 30px;
}
blockquote p:after{
    content: close-quote;
    font-weight: bold;
    font-size: 30px;
}

.teachingPhilosophy{
    margin-top: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.teachingPhilosophy img{
    margin: 10px;
}

.subjectContainer{
    padding-left: 5vw;
    padding-right: 5vw;
}

.row {
    display: flex;
    justify-content: center;
}

.cols{
    border: 2px solid;
    border-color: rgb(92,175,255);
    text-align: center;
    margin: 10px;
    width: 30%;
}

.subjectBox img{
    width: 50%;
}

.ageDescription{
    margin-top: 20px;
}

@media screen and (max-width: 1200px){
    blockquote p {
        font-size: 20px;
    }

    Footer p{
        font-size: 30px;
    }

    .teachingPhilosophy{
        margin-top: 10px;
        display: block;
    }
    .teachingPhilosophy img{
        margin: 10px;
        width: 70vw;
    }

    .cols{
        width: 100%;
    }
}