.testimonials{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 10%;
}

.testimonials div {
    height: 100%;
    width: 100%;
    text-align: center;
}

.testimonials div:hover img{
    opacity: .5;
}

.testimonials div:hover .testimonialText{
    visibility: visible;
    text-align: center;
    width: 75%;
}

.testimonials img {
    width: 80vw;
    height: auto
}

.testimonialText{
    background-color: rgb(92,175,255);
    visibility: hidden;
    padding: 25% 5%;
    height: 80%;
}

