.extraSpace{
    float:left;
    width: 15vw;
    height: 10vh;
}

ul{
    text-align: center;
}

.list-inline li {
    display: inline-block;
    padding: 10px;
    float: left;
}

.contactIcon{
    font-size: 10vw;
}


li:hover{
    cursor: pointer;
}

.bannerContainer{
    background-color: black;
    width: 100%;
    height: 50vh;
    text-align: center;
    z-index: 1;
    visibility: hidden;
    color: white;
    font-size: 2em;
    display: none;
}


.CODE {
    padding-top: 20px;
    height: 70%;
    display: block;
    margin-left: auto;
    margin-right: auto;
}

@keyframes fadeIn{
   0% {
        opacity: 0;
   }
   100%{
        visibility: visible;
        opacity: 1;
   }
}

.bannerContainer:hover{
   visibility: visible;
   display: inline-block;
}

#email:hover ~ #emailBanner{
    visibility: visible;
    animation: fadeIn .3s ease-in-out forwards;
    display: inline-block;
}

#phone:hover ~ #phoneBanner{
    visibility: visible;
    animation: fadeIn .3s ease-in-out forwards;
    display: inline-block;
}
#linked:hover ~ #linkedBanner{
    visibility: visible;
    animation: fadeIn .3s ease-in-out forwards;
    display: inline-block;
}
#messenger:hover ~ #messengerBanner{
    visibility: visible;
    animation: fadeIn .3s ease-in-out forwards;
    display: inline-block;
}
#github:hover ~ #githubBanner{
    visibility: visible;
    animation: fadeIn .3s ease-in-out forwards;
    display: inline-block;
}

@media screen and (max-width: 1200px){
    .contactIcon{
        font-size: 50vw;
        margin: 0 auto;
    }

    .contactContainer{
        text-align: center;
    }

    .contactContainer ul{
        display: inline-block;
        margin: 0
    }

    .contactContainer ul li{
        display: inline;
        margin-left: 23vw;
    }

    .extraSpace{
        width: 0;
    }

    .bannerContainer{
        font-size: 20px;
    }

}