.mainPhotoMobile{
 height: 100vh;
}

.mainPhoto{
    position: fixed;
    left: 0;
    top: 0;
    background-image: url("./Blueprint Table.jpg");
    width: 100%;
    height: 100%;
    min-height: 100%;
    margin: 0;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
   }

.tool {
    position: relative;
    display: flex;
    overflow: hidden;
    background-repeat: no-repeat;
    background-position: center;
    justify-content: center;
    align-items: center;
}

.tool:hover{
    cursor: all-scroll;
}

