.testimonialContainer{
    width: 100%;
    display: flex;
    justify-content: center;
}

.testimonial {
  display: flex;
  justify-content: center;
  background-color: white;
  position: relative;
  height: 50vh;
  width: 25vw;
  margin: 5%;
  margin-top: 0;
}

.testimonialImage {
  display: block;
  width: 100%;
  height: auto;
  border-radius: 10%;
}

.overlay {
  position: absolute;
  bottom: 100%;
  left: 0;
  right: 0;
  background-color: #008CBA;
  overflow: hidden;
  width: 100%;
  height:0;
  transition: .5s ease;
  border-radius: 10%;
}

.overlay div{
    display: block;
    text-align: center;
    padding: 10%;
    width: 100%;
    font-size: 2vh;
    color: white;
}

.testimonial:hover .overlay {
  bottom: 0;
  height: 100%;
}

@media screen and (max-width: 1200px){

    .testimonialContainer{
        width: 100%;
        display: block;
    }

    .testimonial{
        width: 70%;
        height: auto;
        margin: 10px auto;
        margin-bottom: 40px;
    }

    .overlay div {
        font-size: 2.5vw;
    }

    .overlay div h3{
        font-size: 3.5vw;
    }
}