.aboutHolder{
    width: 100%;
    text-align: center;
    display: flex;
    justify-content: center;
}

.carousel {
    width: 50vh;
    float: left;
    margin: 30px;
    margin-left: 10%;
}

.carousel img {
    width: 100%;
    border-radius: 30px;
}


.information{
    width: 50%;
    margin-left: 50px;
    padding-top: 50px;
    padding-right: 50px;
    text-align: left;
}

@media screen and (max-width: 1200px) {
    .aboutHolder{
        display: flex;
        justify-content: center;
    }

    .carousel {
        width: 70vw;
    }

    .information{
        display: inline-block;
        margin: 30px;
        padding-top: 10px;
        width: 90%;
    }

}