.projectsHolder{
    width: 100%;
    padding-bottom: 5vh;
    display: flex;
    justify-content: center;
}

.projectsHolder img {
    position: relative;
    width: 20vw;
    border-radius: 10%;
}

.contentText {
    padding-top: 20px;
    position: relative;
    margin-left: 20px;
}

@media screen and (max-width: 1200px){

    .projectsHolder{
        display: block;
    }

    .projectsHolder img {
        display: block;
        margin: 0 auto;
        width: 80vw;
        border-radius: 10%;
        width: 80vw;

    }

    .contentText{
        padding: 15px;
    }

}
